import topBar from '../../components/topBar.vue'
export default {
  components: {
    topBar: topBar,
  },
  data() {
    return {
      num: 0,                // 当前订单消耗积分数量
      order_id: '',          // 订单id
    }
  },
  onLoad(opt) {
    console.log(opt.num)
    if(opt.num) {
      this.num= opt.num
      this.order_id= opt.order_id
    }
  },
  methods: {
    // 完成，返回首页
    achieve() {
      console.log('点击了完成')
      uni.switchTab({
        url: '../integral'
      })
    },
    orderDetail() {
      uni.navigateTo({
        url: 'orderDetail?order_id=' + this.order_id
      })
    }
  }
}
